import tuiCountdown from "@/components/countdown/countdown"
import tarbar from "@/components/tarbar/tarbar"
export default {
	components: {			
		tuiCountdown,
		tarbar
	},
	data() {
		return {
			headerPosition: "fixed",
			tabIndex: 0,
			timeList: [],
			goodsList:[],
			stime:'',
			etime:'',
			seckillTime:0,
		}
	},
	onPageScroll(e) {
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	onLoad() {
		this.getTime()
	},
	onShow() {
		
	},
	methods: {
		getTime(){
			this.$xm.post('/Index/seckill','',(res)=>{					
				res.data.forEach(ele=>{
					if(ele.status==1){
						ele.state='倒计时';							
						this.seckillTime = parseInt(ele.etime) - Math.round(new Date() / 1000);
						console.log(this.seckillTime);
					}else if(ele.status==2){
						ele.state='即将开始';
						let t=this.$xm.changeTime(ele.stime*1000)
						ele.time=t.slice(12,t.length);
					}
				})
				console.log(res.data);
				this.timeList=res.data;
				this.stime=res.data[0].stime;
				this.etime=res.data[0].etime;
				this.getGoodS()
			})
		},
		getGoodS(){
			let data={
				stime:this.stime,
				etime:this.etime
			}
			this.$xm.post('/Index/seckillpro',data,(res)=>{
				this.goodsList=res.data;
				this.goodsList.forEach(ele=>{
					ele.pro_sn='http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300'
				})
			})
		},
		changeList(e,i){
			this.stime=e.stime;
			this.etime=e.etime;
			this.tabIndex=i;
			this.getGoodS()
		},
		toGoods(goods){
			uni.navigateTo({
				url:'../../goods/goods?gid='+goods.id
			})
		},
		joinCart(goods){
			console.log(goods);				
			let data = {
				proid: goods.id,
				pronum: 1,
			};
			this.$xm.post('/Cart/add', data, (res) => {
				let result = res.msg;					
				if (res.s == 1) {
					uni.showToast({
						title: result
					});
				}else if(res.s == 0) {
					uni.showToast({
						title: result,
						icon:'none'
					});
				}
				this.$xm.post('/Cart', '', res => {
					uni.setStorage({
						key: 'cart',
						data: res.count,
						success: (res) => {
							uni.getStorage({
								key: 'cart',
								success: (res) => {
									let count = res.data;
									if (res.data !== null) {
										count = count.toString();
										uni.setTabBarBadge({
											index: 2,
											text: count
										})
									}
								}
							})
						}
					})
				})
			})
			
		}
	}
}
